___CSS_LOADER_EXPORT___.push([module.id, `.modalInput {
  display: none;
}

.modalButton {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  position: fixed;
  width: 90px;
  height: 90px;
  bottom: 10%;
  left: 85vw;
  border-radius: 50%;
  background: #3f3d3d;
  color: #FFFFFF;
  box-shadow: 0 5px 10px 0 rgb(101, 90, 106);
  z-index: 3;
}
.modalButton::before {
  content: "Open modal";
}
.modalButton:hover {
  animation: 0.5s linear 0s 1 both hover_button;
}
.modalButton:active {
  transform: translate(2px, 2px);
  box-shadow: none;
}

@keyframes hover_button {
  from {
    background: #3f3d3d;
    color: #FFFFFF;
  }
  to {
    background: #DADADA;
    color: #3f3d3d;
  }
}
.modalWrapper {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: rgba(3, 3, 3, 0.4);
  visibility: hidden;
}

#modal:checked ~ .modalWrapper {
  animation: 0.5s linear 0s 1 both open_modal;
}

@keyframes open_modal {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
    visibility: visible;
  }
}
#modal:checked ~ .modalButton::before {
  content: "Close modal";
}

.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: #FFFFFF;
  width: 30%;
  height: 30%;
  font-size: 20px;
  text-align: center;
